  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
footer .logo-footer img {
  width: 3rem;
}
footer #copy-right {
  font-size: 1rem;
  margin-top: 0.5rem;
}

main {
  width: 100%;
  height: max-content;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 80vh;
}
