}

/* Breakpoint untuk mobile 576px */
@media screen and (min-width: 576px) {
  body {
    background-color: var(--primary-color);
  }
  header svg {
    display: none;
  }
  header .header a h1 {
    color: var(--tertiary-color);
  }
  header .header a:focus h1 {
    color: var(--dark-color);
  }
  nav#navbar-big {
    display: flex;
  }
  header {
    display: flex;
    justify-content: space-around;
    background-color: inherit;
    height: fit-content;
    color: var(--tertiary-color);
    position: sticky;
    top: 0;
  }
  #break {
    display: inline;
  }
  nav#navbar-big ul {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 2rem;
  }
  header #navbar-big ul li a {
    color: var(--dark-color);
    border-radius: 5px;
    padding: 5px 20px;
    border: 2px solid none;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    outline: var(--tertiary-color);
  }
  body {
    height: min-content;
  }
  header #navbar-big ul li a:hover, header #navbar-big ul li a:focus {
    color: var(--primary-color);
    background-color: var(--tertiary-color);
    outline: none;
  }
  header #drawer-button, header .mobile-sidebar {
    display: none;
  }
  main .hero {
    min-width: 100%;
    text-align: center;
    background-color: black;
  }
  footer {
    padding-bottom: 1.5rem;
    min-height: 8rem;
  }
  main #daftar-restoran {
    display: grid;
    place-items: center;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 0.5rem 2rem;
    padding-left: 10%;
    padding-right: 10%;
  }
  main .hero::before {
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.65)), url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  }

  .active-nav {
    color: var(--primary-color);
    background-color: var(--tertiary-color);
    outline: none;
  }
}

/* Breakpoint untuk tablet 768px */
@media screen and (min-width: 768px) {
  main .hero {
    min-height: 30rem;
  }
  main #daftar-restoran {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(325px, 1fr));
    gap: 0.5rem 2rem;
    padding-left: 15%;
    padding-right: 15%;
    padding-top: 5rem;
  }

  main .hero::before {
    background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.65)), url(${___CSS_LOADER_URL_REPLACEMENT_3___});
  }
